import axios from "axios";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import { Button, CardBody, CardHeader } from "reactstrap";
import {Card} from "reactstrap";

import "react-toastify/dist/ReactToastify.css";
import "./Register.css";

const Result = (props) => {
  const [flag, setFlag] = useState(false);
  const [bookData, setBookData] = useState(null);
  const [booksData, setBooksData] = useState(null);
  const book = localStorage.getItem('Books')
  const books = JSON.parse(book);
  
  const navigate = useNavigate();

  const [formData, setFormData] = useState({
         name:''
       });
  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
  
    const { name } = formData; // Assuming formData contains the book name
  
    try {
      const response = await axios.post(
        "http://localhost:5000/search",
        { name: name }, 
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      );
      const { book, signature } = response.data;
      const isSignatureValid = await verifySignature(name, signature);
      if (isSignatureValid) {
        // Signature is valid, set the book data
        setBookData(book);
        setFlag(true);
      } else {
        // Signature is invalid, display an error
        toast.error('Signature verification failed');
      }

    } catch (error) {
      console.error("Axios error:", error.response.data.error);
      toast.error(error.response.data.error);
    }
  };

  const handleDownload = async (e) => {
    e.preventDefault();
  
    const { name } = formData; // Assuming formData contains the book name

    try {
      const response = await axios.get(
        "http://localhost:5000/download", 
        {
           responseType: 'json',
        }
      );
      const { book, signature } = response.data;
      setBooksData(book)
      const isSignatureValid = await verifyBookData(booksData,signature);
      console.log(isSignatureValid)
      if (isSignatureValid) {
        // Signature is valid, set the book data
        toast.success('Verified. Your book will be downloaded')
      } else {
        // Signature is invalid, display an error
        toast.error('Signature verification failed');
      }

    } catch (error) {
      console.error("Axios error:", error.response.data.error);
      toast.error(error.response.data.error);
    }
  };

  const verifySignature = async (data, signature) => {
    try {
        // Fetch the public key from the server or any trusted source
        const publicKeyResponse = await axios.get("http://localhost:5000/public_key");
        // Assuming the public key is returned as a string
        let publicKey = publicKeyResponse.data;
        publicKey = publicKey.replace(/-----BEGIN PUBLIC KEY-----/, '');
        publicKey = publicKey.replace(/-----END PUBLIC KEY-----/, '');
        publicKey = publicKey.replace(/\r?\n|\r/g, ''); // Remove newlines
        // Convert the public key string to CryptoKey object
        const cryptoKey = await window.crypto.subtle.importKey(
            "spki",
            Uint8Array.from(atob(publicKey), c => c.charCodeAt(0)), // Convert base64 to Uint8Array
            {
                name: "RSASSA-PKCS1-v1_5",
                hash: { name: "SHA-256" },
            },
            true,
            ["verify"]
        );

        // Convert the signature from hex to ArrayBuffer
        const signatureBytes = hexStringToArrayBuffer(signature);

        // Convert the data to Uint8Array
        const dataBytes = new TextEncoder().encode(data);
        // Verify the signature
        const isSignatureValid = await window.crypto.subtle.verify(
            "RSASSA-PKCS1-v1_5",
            cryptoKey,
            signatureBytes,
            dataBytes
        );
        return isSignatureValid;
    } catch (error) {
        console.error("Signature verification error:", error);
        return false;
    }
};

const verifyBookData = async (bookData, signature) => {
  try {
      // Fetch the public key from the server or any trusted source
      const publicKeyResponse = await axios.get("http://localhost:5000/public_key");
      // Assuming the public key is returned as a string
      let publicKey = publicKeyResponse.data;
      publicKey = publicKey.replace(/-----BEGIN PUBLIC KEY-----/, '');
      publicKey = publicKey.replace(/-----END PUBLIC KEY-----/, '');
      publicKey = publicKey.replace(/\r?\n|\r/g, ''); // Remove newlines
      // Convert the public key string to CryptoKey object
      const cryptoKey = await window.crypto.subtle.importKey(
        "spki",
        Uint8Array.from(atob(publicKey), c => c.charCodeAt(0)), // Convert base64 to Uint8Array
        {
          name: "RSASSA-PKCS1-v1_5",
          hash: { name: "SHA-256" },
        },
        true,
        ["verify"]
      );
      
      // Convert the signature from hex to ArrayBuffer
      const signatureBytes = Uint8Array.from(atob(signature), c => c.charCodeAt(0));
      // const signatureBytes = hexStringToArrayBuffer(signature);

      // Convert the book data to Uint8Array
      const bookDataBytes = Uint8Array.from(atob(bookData), c => c.charCodeAt(0));

      // Verify the signature
      const isSignatureValid = await window.crypto.subtle.verify(
          "RSASSA-PKCS1-v1_5",
          cryptoKey,
          signatureBytes,
          bookDataBytes
      );
      return isSignatureValid;
  } catch (error) {
      console.error("Signature verification error:", error);
      return false;
  }
};

// Function to convert hex string to ArrayBuffer
function hexStringToArrayBuffer(hexString) {
    const bytes = new Uint8Array(Math.ceil(hexString.length / 2));
    for (let i = 0; i < bytes.length; i++) {
        bytes[i] = parseInt(hexString.substr(i * 2, 2), 16);
    }
    return bytes.buffer;
}


  const handleLogout = async () => {
    // Perform your logout logic here
    try {
      const response = await axios.post("http://localhost:5000/logout");
      if (typeof props.token === "function") {
        props.token(); // Assuming this function is responsible for handling the token
      }
      navigate("/");
      localStorage.removeItem("token");
    } catch (error) {
      console.error("Axios error:", error);
    }
  };
  

  return (
   <div className="register-container">
          <h1 className="big-font">Welcome to our online library!!</h1>
    <form onSubmit={handleSubmit} className="form-box">
      <div className="form-group">
        <label style ={{width:'100%'}} htmlFor="name">Enter the book title</label>
        <input
        style ={{width:'100%'}}
          type="text"
          id="name"
          name="name"
          value={formData.name}
          onChange={handleChange}
          required
          />
      </div>
      <button type="submit">Submit</button>
    </form>
          {!flag && (
            <div>
  <h1> Few titles are:</h1>
  <h4>Brave New World</h4>
  <h4>1984</h4>
  </div>
  )}
  {flag && bookData && ( // Only render if flag is true and bookData is available
          <div>
          <Card className="cards-container" style={{width:'50%',marginTop:'1rem'}}>
          <CardHeader >
          <CardBody style={{width:'100%'}}> 
          <h2>{bookData.title}</h2>
          <p>Author: {bookData.author}</p>
          <p>Synopsis:It's a literary fiction story about change versus tradition. It kicks off in a butcher shop with someone appealing to an authority figure.</p>
          <p>Year Published: {bookData.year_published}</p>
          <Button onClick={handleDownload} style={{ marginTop:'2rem',marginLeft: "1rem",position:'absolute',bottom: '5px',right: '10px',marginleft: '0'}}>Download</Button>
          </CardBody>
        </CardHeader>
          </Card>
          </div>

  )}
          <Button
          style={{ marginLeft: "1rem", backgroundColor:'#cf1338',position:' absolute',top: '10px',right: '10px'}}
          color="danger"
          onClick={handleLogout}
        >
          Logout
        </Button>
        <ToastContainer />
  </div>
  
  )
}

export default Result