import axios from "axios";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "./Register.css";

const ForgotPassword = () => {
    const [flag, setFlag] = useState(false);
    const [password, setPassword] = useState(false);
    const [securityQuestion, setSecurityQuestion] = useState('');
    const [formData, setFormData] = useState({
        name:'',
        answer:'',
        password: '',
        confirm_password: ''
      });

      const navigate = useNavigate();
    
      const handleChange = (e) => {
        setFormData({
          ...formData,
          [e.target.name]: e.target.value,
        });
      };
      const handleSubmit = async (e) => {
        e.preventDefault();
    
        const { name  } = formData;
    
        try {
          const response = await axios.post(
            "http://localhost:5000/reset",
            formData,
            {
              headers: {
                "Content-Type": "application/json",
              },
            }
          );
          console.log(response);
          setSecurityQuestion(response.data.security_question);
          setFlag(true);
        //   navigate('/')
        } catch (error) {
          console.error("Axios error:", error.response.data.error);
          toast.error(error.response.data.error);
        }
      };
      const handleVerifySubmit = async (e) => {
        e.preventDefault();

        const { name, answer } = formData;

        try {
            const response = await axios.post(
                "http://localhost:5000/verify",
                { name, answer },
                { headers: { "Content-Type": "application/json" } }
            );
            console.log(response.data.message);
            setFlag(true);
            setPassword(true)
        } catch (error) {
            console.error("Axios error:", error.response.data.error);
            toast.error(error.response.data.error);   
        }
    };

    const handlePasswordChange = async(e) =>{
      e.preventDefault();
      const{name,answer,password,confirm_password} = formData;
      try{
        const response = await axios.post(
          "http://localhost:5000/change",
                { name, answer,password,confirm_password},
                { headers: { "Content-Type": "application/json" } }
        );
        console.log(response.data.message);
        navigate('/')
        toast.success('Password changed successfully');
      }catch(error){
        console.error("Axios error:", error.response.data.error);
        toast.error(error.response.data.error);
      }
    }

      return (
        <div className="register-container">
          <h1 className="big-font">Change Password</h1>
          {!flag && (
    <form onSubmit={handleSubmit} className="form-box">
      <div className="form-group">
        <label htmlFor="name">Enter Your Name</label>
        <input
          type="text"
          id="name"
          name="name"
          value={formData.name}
          onChange={handleChange}
          required
        />
      </div>
      <button type="submit">Submit</button>
    </form>
  )}
            {flag && !password && (
              <>
                <form onSubmit={handleVerifySubmit} className="form-box">
                        <div className="form-group">
                            <label htmlFor="security_question">Security Question</label>
                            <p>Security Question: {securityQuestion}</p>
                        </div>
                        <div className="form-group">
                            <label htmlFor="answer">Security Answer</label>
                            <input
                                type="text"
                                id="answer"
                                name="answer"
                                value={formData.answer}
                                onChange={handleChange}
                                required
                                onSubmit={handleVerifySubmit}
                            />
                        </div>
                        <button type="submit">Submit</button>
                </form>
                    </>
                )}
            {flag && password &&(
              <>
              <form onSubmit={handlePasswordChange} className="form-box">
              <label htmlFor="password">Password</label>
              <div className="form-group">
              <input
                type="password"
                id="password"
                name="password"
                value={formData.password}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group">
              <label htmlFor="confirm_password">Confirm Password</label>
              <input
                type="password"
                id="confirm_password"
                name="confirm_password"
                value={formData.confirm_password}
                onChange={handleChange}
                required
                />
            </div>
            <button type="submit">Submit</button>
            </form>
              </>
            )
            } 

          <ToastContainer />
        </div>
      );
}

export default ForgotPassword