// LoginRegisterPage.jsx
import axios from "axios";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Container, Form, FormGroup } from "reactstrap";
import "./LoginRegisterPage.css";
import { styled } from "@mui/material/styles";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";



const LoginRegisterPage = (props) => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");

  const [emptyInputError, setEmptyInputError] = useState(false); // State for empty input error

  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    email: "",
    password: "",
  });

  const BootstrapButton = styled(Button)({
    boxShadow: "none",
    textTransform: "none",
    fontSize: 12,
    padding: "6px 12px",
    border: "1px solid",
    lineHeight: 1.5,
    backgroundColor: "#0063cc",
    borderColor: "#0063cc",
    fontFamily: [
      "-apple-system",
      "BlinkMacSystemFont",
      '"Segoe UI"',
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(","),
    "&:hover": {
      backgroundColor: "#0069d9",
      borderColor: "#0062cc",
      boxShadow: "none",
    },
    "&:active": {
      boxShadow: "none",
      backgroundColor: "#0062cc",
      borderColor: "#005cbf",
    },
    "&:focus": {
      boxShadow: "0 0 0 0.2rem rgba(0,123,255,.5)",
    },
  });

  const handleLogin = async (e) => {
    e.preventDefault();

    try {
      const response = await axios.post(
        "http://localhost:5000/login",
        formData,
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      );
      const { access_token, message, books } = response.data;
      if (access_token) {
        props.setToken(access_token);
        localStorage.setItem("Books", JSON.stringify(books));
        localStorage.setItem("token", access_token); // Navigate to the "/recruit" route
        navigate("/search");
      } else {
        console.log(message);
      } 
    } catch (error) {
      if (error.response && error.response.status === 401) {
        toast.error("Please check your email and password");
      } else {
        toast.error("Something went wrong please try again");
      }
    }
  };

  const handleRestPassword = async (e) => {
    e.preventDefault();
    navigate("/reset");
    
  };

  const handleRegister = () => {
    // Handle registration logic
    navigate("/register");
    console.log("Registering with:", { username, password });
  };

  return (
    <div className="login-register-page">
      <Container>
        <Form className="login-register-form">
          <h1 style = {{color: "#12192c"}}>Login</h1>
          {/* {emptyInputError && (
            <Alert color="danger">
              Please enter both username and password.
            </Alert>
          )} */}
          {/* {loginError && (
            <Alert color="danger">Invalid credentials. Please try again.</Alert>
          )} */}
          <FormGroup
            className={`custom-input ${emptyInputError ? "error" : ""}`}
          >
            <TextField
              id="outlined-required"
              style={{ marginBottom: "0.5rem" }}
              type="text"
              value={formData.email}
              onChange={(e) =>
                setFormData({ ...formData, email: e.target.value })
              }
              placeholder="Email ID"
            />
          </FormGroup>
          <FormGroup
            className={`custom-input ${emptyInputError ? "error" : ""}`}
          >
            <TextField
              id="outlined-password-input"
              style={{ margin: "0.5rem" }}
              type="password"
              value={formData.password}
              onChange={(e) =>
                setFormData({ ...formData, password: e.target.value })
              }
              placeholder="Password"
            />
          </FormGroup>
          <div>
            <Button style={{marginTop:'0.5rem' }} onClick={handleRegister} color="error">
              Register
            </Button>
            <BootstrapButton
              style={{marginTop:'0.5rem', marginLeft: "12rem" }}
              onClick={handleLogin}
              variant="contained"
              disableRipple
            >
              Login
            </BootstrapButton>
            <BootstrapButton
              style={{marginTop:'0.5rem',marginLeft:'0.25rem', marginRight: "0.5rem" }}
              onClick={handleRestPassword}
              variant="contained"
              disableRipple
            >
              Forgot Password
            </BootstrapButton>
          </div>
        </Form>
      </Container>
      <ToastContainer />
    </div>
  );
};

export default LoginRegisterPage;
